:root{
    --var-fundo:rgb(16, 187, 0);
}


.cima{
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #fff;
    border-radius: 30px;
}

.logo{
    margin-left: 40px;
}

.outrasParada{
    display: flex;
    height: 100%;
    margin-right: 10%;
    gap: 10px;
}

.btn{
    display: flex;
    padding: 5px;
    font-size: 30px;
    align-items: center;
    color: #fff;
    transition: color 1s;
}
.btn:hover{
    cursor: pointer;
    color: var(--var-fundo);
}

.boasVindas{
    display: flex;
    font-size: 40px;
}